const actions = {
  getCashFlowTypes: (page, search, filter) => ({
    type: 'GET_CASH_FLOW_TYPES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createCashFlowType: () => ({
    type: 'CREATE_CASH_FLOW_TYPE_REQUEST'
  }),
  storeCashFlowType: data => ({
    type: 'STORE_CASH_FLOW_TYPE_REQUEST',
    payload: { body: data }
  }),
  editCashFlowType: id_cash_flow_type => ({
    type: 'EDIT_CASH_FLOW_TYPE_REQUEST',
    payload: { id_cash_flow_type: id_cash_flow_type }
  }),
  updateCashFlowType: (id_cash_flow_type, data) => ({
    type: 'UPDATE_CASH_FLOW_TYPE_REQUEST',
    payload: {
      id_cash_flow_type: id_cash_flow_type,
      body: data
    }
  }),
  showCashFlowType: id_cash_flow_type => ({
    type: 'SHOW_CASH_FLOW_TYPE_REQUEST',
    payload: { id_cash_flow_type: id_cash_flow_type }
  }),
  deactivateCashFlowType: id_cash_flow_type => ({
    type: 'DEACTIVATE_CASH_FLOW_TYPE_REQUEST',
    payload: { id_cash_flow_type }
  }),
  activateCashFlowType: id_cash_flow_type => ({
    type: 'ACTIVATE_CASH_FLOW_TYPE_REQUEST',
    payload: { id_cash_flow_type }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
