import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import CashFlowTypeUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/cashBank/cashFLowType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/cash-flow-types";

class UpdateCashFlowType extends Component {

  componentDidUpdate() {
    // Display error notif
    if (this.props.cashFlowType.error) {
      notification("error", this.props.cashFlowType.error);
      this.props.clearError();
    }

    if (this.props.cashFlowType.success) {
      this.props.history.replace(redirectTo);
    }
  }

  // Get All data diawal
  componentDidMount() {
    const { editCashFlowType } = this.props;
    const { id } = this.props.match.params;
    editCashFlowType(id);
  }

  handleSubmit = value => {
    const { updateCashFlowType } = this.props;
    const { id } = this.props.match.params;
    updateCashFlowType(id, value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="cashFlowTypes.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.cashFlowType.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <CashFlowTypeUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                cashFlowType={this.props.cashFlowType}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, cashFlowType: state.cashFlowType };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateCashFlowType);
