import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Divider, Row, Col, Alert } from "antd";

const { Option } = Select;
const { TextArea } = Input;

class CashFlowTypeUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        if(values.transaction_type === 1){
          values.default_debit_memo = 0
          values.default_payable_payment = 0
        }
        else {
          values.default_credit_memo = 0
          values.default_receivable_payment = 0
        }
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError, getFieldValue } = this.props.form;
    const { cashFlowType } = this.props;
    const { loading } = cashFlowType;
    const data = cashFlowType.cash_flow_type ? cashFlowType.cash_flow_type : [];
    const used = data.is_used;
    const error = cashFlowType.errorField ? cashFlowType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="cashFlowType.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="cashFlowTypes.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="cashFlowTypes.add.code" />}
              validateStatus={
                error.cash_flow_type_code || getFieldError("cash_flow_type_code")
                  ? "error"
                  : null
              }
              help={
                error.cash_flow_type_code
                  ? error.cash_flow_type_code
                  : getFieldError("cash_flow_type_code")
              }
            >
              {getFieldDecorator("cash_flow_type_code", {
                initialValue: data.cash_flow_type_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="name" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </Form.Item>
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="cashFlowGroup" />}
            >
              {getFieldDecorator("id_cash_flow_group", {
                initialValue: data.id_cash_flow_group,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={cashFlowType.loadingFilterCashFLowGroups}
                  showSearch
                  optionFilterProp="children"
                  disabled={used ? true : false}
                >
                  {this.props.cashFlowType.cash_flow_groups.map((data, key) => (
                    <Option key={key} value={data.id_cash_flow_group}>
                      {data.description}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="transactionType" />}
            >
              {getFieldDecorator("transaction_type", {
                initialValue: data.transaction_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group style={{ display: "flex", width: "50%" }} buttonStyle="solid" disabled={used ? true : false}>
                  <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="deposit" /></Radio.Button>
                  <Radio.Button style={{ flex: "1", textAlign: "center" }} value={2}><IntlMessages id="withdrawal" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
          </div>
        </div>

        {
          getFieldValue('transaction_type')
          ?
            (
              <Divider orientation="left" style={{ fontSize: 14 }}>
                <IntlMessages id="cashFlowTypes.add.subtitle.default" />
              </Divider>
            )
          : null
        }

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            {
              getFieldValue('transaction_type') === 1
              ? 
                (
                  <>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="cashFlowTypes.add.defaultCreditMemo" />}
                    >
                      {getFieldDecorator("default_credit_memo", {
                        initialValue: data.default_credit_memo ? data.default_credit_memo : 0,
                      })(
                        <Radio.Group style={{ display: "flex", width: "50%" }} buttonStyle="solid">
                          <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="yes" /></Radio.Button>
                          <Radio.Button style={{ flex: "1", textAlign: "center" }} checked value={0}><IntlMessages id="no" /></Radio.Button>
                        </Radio.Group>
                      )}
                    </Form.Item>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="cashFlowTypes.add.defaultReceivablePayment" />}
                    >
                      {getFieldDecorator("default_receivable_payment", {
                        initialValue: data.default_receivable_payment ? data. default_receivable_payment : 0,
                      })(
                        <Radio.Group style={{ display: "flex", width: "50%" }} buttonStyle="solid">
                          <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="yes" /></Radio.Button>
                          <Radio.Button style={{ flex: "1", textAlign: "center" }} checked value={0}><IntlMessages id="no" /></Radio.Button>
                        </Radio.Group>
                      )}
                    </Form.Item>
                    <Form.Item
                      {...formItemLayout}
                      label={<IntlMessages id="cashFlowTypes.add.defaultCashManagementDeposit" />}
                    >
                      {getFieldDecorator("default_cash_management_deposit", {
                        initialValue: data.default_cash_management_deposit ? data.default_cash_management_deposit : 0,
                      })(
                        <Radio.Group style={{ display: "flex", width: "50%" }} buttonStyle="solid">
                          <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="yes" /></Radio.Button>
                          <Radio.Button style={{ flex: "1", textAlign: "center" }} checked value={0}><IntlMessages id="no" /></Radio.Button>
                        </Radio.Group>
                      )}
                    </Form.Item>
                  </>
                )
              :
                getFieldValue('transaction_type') === 2
                ?
                  (
                    <>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="cashFlowTypes.add.defaultDebitMemo" />}
                        
                      >
                        {getFieldDecorator("default_debit_memo", {
                          initialValue: data.default_debit_memo ? data.default_debit_memo : 0,
                        })(
                          <Radio.Group style={{ display: "flex", width: "50%" }} buttonStyle="solid">
                            <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="yes" /></Radio.Button>
                            <Radio.Button style={{ flex: "1", textAlign: "center" }} checked value={0}><IntlMessages id="no" /></Radio.Button>
                          </Radio.Group>
                        )}
                      </Form.Item>

                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="cashFlowTypes.add.defaultPayablePayment" />}
                      >
                        {getFieldDecorator("default_payable_payment", {
                          initialValue: data.default_payable_payment ? data.default_payable_payment : 0,
                        })(
                          <Radio.Group style={{ display: "flex", width: "50%" }} buttonStyle="solid">
                            <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="yes" /></Radio.Button>
                            <Radio.Button style={{ flex: "1", textAlign: "center" }} checked value={0}><IntlMessages id="no" /></Radio.Button>
                          </Radio.Group>
                        )}
                      </Form.Item>

                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="cashFlowTypes.add.defaultCashManagementWithdrawal" />}
                      >
                        {getFieldDecorator("default_cash_management_withdrawal", {
                          initialValue: data.default_cash_management_withdrawal ? data.default_cash_management_withdrawal : 0,
                        })(
                          <Radio.Group style={{ display: "flex", width: "50%" }} buttonStyle="solid">
                            <Radio.Button style={{ flex: "1", textAlign: "center" }} value={1}><IntlMessages id="yes" /></Radio.Button>
                            <Radio.Button style={{ flex: "1", textAlign: "center" }} checked value={0}><IntlMessages id="no" /></Radio.Button>
                          </Radio.Group>
                        )}
                      </Form.Item>
                    </>
                  )
                : null
            }
          </div>
        </div>
  

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
            //disabled={used ? true : false}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedCashFlowTypeUpdateForm = Form.create({ name: "update_cash_flow_type" })(CashFlowTypeUpdateForm);

export default WrappedCashFlowTypeUpdateForm;
